.admin-dashboard-container {
    width: 100%;
}

.admin-panel {
    width: 98vw;
    display: flex;
}

.sidebar {
    width: 19vw;
    height: 82vh;
    background-color: #1e1e2f;
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.logout-admin-wrapper {
    padding: 20px;

}

.logout-btn {
    background-color: #34344e !important;
    font-size: 16px;
    cursor: pointer;
}

.sidebar-inner {
    color: white;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.sidebar button {
    background-color: transparent;
    border: none;
    color: white;
    padding: 10px;
    text-align: left;
    font-size: 16px;
    cursor: pointer;
    border-radius: 8px;
    transition: background 0.3s;
}

.sidebar button:hover {
    background-color: #34344e;
}

.admin-panel-content {
    width: 78vw;
}